.featured-category {
	&__header {
		@include make-container( $grid-container-widths );
		display: flex;
		flex-wrap: wrap;
		align-items: center;

        padding-top: ($default-padding * 2);
        padding-bottom: $default-padding;

		@include media-breakpoint-up(lg) {
			padding-top: ($default-padding * 3);
			padding-bottom: ($default-padding * 3);
		}

		@include media-breakpoint-up(xxl) {
			padding-top: ($default-padding * 5);
			padding-bottom: ($default-padding * 5);
		}

		&__cta {
			margin-top: $mini-margin;
			width: 100%;
			display: none;

			@include media-breakpoint-up(sm) {
				display: block;
				margin-top: 0;
				margin-left: auto;
				width: auto;
			}

			a {
				border-bottom: 1px solid $color-dark-grey;

				.white & {
					border-bottom-color: $color-white;
				}

				&:hover {
					opacity: 0.6;
					border-bottom-color: transparent;
				}
			}
		}
	}

	&__title {
		@include large-serif-title;
	}

	&__description {
		@include title;
		font-weight: 700;
	}

	&__posts {
		.tns-controls {
			@include make-container( $grid-container-widths );
			display: flex;
			padding-top: $large-padding;
			padding-bottom: $default-padding;
			outline: 0;
			justify-content: flex-end;

			@include media-breakpoint-up(lg) {
				padding-top: ($default-padding * 2);
				padding-bottom: ($default-padding * 2);
			}

			[data-controls] {
				font-size: 36px;
				line-height: 36px;
				height: 36px;
				margin: 0;
				padding: 0;
				transition-duration: 500ms;
				transition-property: opacity;
				background-color: transparent;
				appearance: none;

				@include media-breakpoint-up(lg) {
					font-size: 48px;
					line-height: 48px;
					height: 48px;
				}

				&:before {
					@include icon(icon-arrow-left);
				}
			}

			[disabled] {
				opacity: 0.2;
			}

			[data-controls="next"] {
				margin-left: $mini-margin;

				&:before {
					@include icon(icon-arrow-right);
				}
			}
		}
	}

	&__cta {
		display: block;
		padding: 0 $default-padding $large-padding;
		margin-top: -$very-large-margin;

		@include media-breakpoint-up(sm) {
			display: none;
		}

		a {
			position: relative;
			z-index: 2;
			border-bottom: 1px solid $color-dark-grey;

			.white & {
				border-bottom-color: $color-white;
			}

			&:hover {
				opacity: 0.6;
				border-bottom-color: transparent;
			}
		}
	}
}