:root {
    --replace-offset: 0%;
    --replace-offset-2: calc((100% - var(--replace-offset)) * -1)
}

.fl-symbol {
    $root: &;
    position: fixed;
    z-index: 9;
    top: 20px;
    left: 20px;

    @include media-breakpoint-up(lg) {
        top: 30px;
        left: 30px;
    }

    .admin-bar & {
        top: 52px;

        @include media-breakpoint-up(lg) {
            top: 62px;
        }

        @media screen and (max-width:782px) {
            top: 66px;
        }
    }

    &--hamburger {
        width: 20px;
        left: auto;
        right: 20px;

        @include media-breakpoint-up(lg) {
            left: auto;
            right: 30px;
        }
    }

    &__container {
        position: relative;

        &.reverse {
            #{$root}__item {
                transform: translateY(calc(var(--replace-offset-2) * 1)); 

                &--active {
                    transform: translateY(calc(var(--replace-offset) * 1));  

                    #{$root}__content {
                        transform: translateY(calc(var(--replace-offset) * -1));
                    }
                }
            }

            #{$root}__content {
                transform: translateY(calc(var(--replace-offset-2) * -1));
            }
        }
    }

    &__item {
        position: absolute;
        top: 0;
        overflow: hidden;
        transform: translateY(calc(var(--replace-offset) * 1));
        will-change: transform;

        &--active {
            transform: translateY(calc(var(--replace-offset-2) * 1));

            #{$root}__content {
                transform: translateY(calc(var(--replace-offset-2) * -1));
            }
        }
    }

    &__content {
        transform: translateY(calc(var(--replace-offset) * -1));
        will-change: transform;
    }

    &__logo {
        width: 36px;
        height: 36px;

        svg {
            width: 100%;
            height: auto;
            fill: $color-dark-grey;

            .navigation-opened & {
                fill: $color-white;
            }
        }

        &--invert {
            svg {
                fill: $color-white;
            }
        }
    }
}