.header {
	position: absolute;
	z-index: 9;
	width: 100%;
	height: 74px;

	@include media-breakpoint-up(lg) {
		height: 82px;
	}

	@include media-breakpoint-up(xxl) {
		height: 95px;
	}

	&__container {
		width: 100%;
		display: flex;
		justify-content: center;
		height: 74px;

		@include media-breakpoint-up(lg) {
			height: 84px;
		}

		@include media-breakpoint-up(xxl) {
			height: 95px;
		}
	}

	&__row {
		width: 100%;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-end;
	}
}
