.site {
	position: relative;
	overflow: hidden;
	background-color: $color-white;
	min-height: var(--app-height);
	display: flex;
	flex-direction: column;
	min-height: 100vh;

	.admin-bar & {
		min-height: calc( 100vh - 32px );

		@media screen and (max-width:782px) {
			min-height: calc( 100vh - 46px );
		}
	}
}
