.pagination {
	margin-top: ($default-margin * 2);

	@include media-breakpoint-up(lg) {
		margin-top: ($default-margin * 3);
	}

	@include media-breakpoint-up(xxl) {
		margin-top: ($default-margin * 5);
	}

	&__list {
		display: flex;
		justify-content: center;
	}

	&__link {
		> * {
			@include large-serif-title;
			opacity: 0.6;
			position: relative;

			&:hover {
				opacity: 1;

				&:after {
					transform: scale(1, 1);
				}
			}

			&:after {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 2px;
				background-color: $color-dark-grey;
				content: '';
				display: block;
				transform: scale(0, 1);
				transition-duration: 300ms;
				transform-origin: left top;

				.white & {
					background-color: $color-white;
				}

				@include media-breakpoint-up(lg) {
					height: 3px;
				}
			}
		}

		span {
			opacity: 1;

			&:after {
				transform: scale(1, 1);
			}
		}
	}

	&__link + &__link {
		margin-left: $default-margin;

		@include media-breakpoint-up(xxl) {
			margin-left: $large-margin;
		}
	}
}