.post-archive {
	padding-top: 95px;

	&.white {
		color: $color-white;
	}

	&__container {
		@include make-container( $grid-container-widths );
		padding-top: ($default-padding * 2);
		padding-bottom: ($default-padding * 2);

		@include media-breakpoint-up(lg) {
			padding-top: ($default-padding * 3);
			padding-bottom: ($default-padding * 3);
		}

		@include media-breakpoint-up(xxl) {
			padding-top: ($default-padding * 5);
			padding-bottom: ($default-padding * 5);
		}
	}

	&__header {
		max-width: 1080px;
		margin-left: auto;
		margin-right: auto;
		text-align: center;
		padding-bottom: ($default-padding * 2);

		@include media-breakpoint-up(lg) {
			padding-bottom: ($default-padding * 3);
		}

		@include media-breakpoint-up(xxl) {
			padding-bottom: ($default-padding * 5);
		}
	}

	&__title {
		@include large-title;
	}

	&__description {
		@include serif-title;
		margin-top: $default-margin;

		@include media-breakpoint-up(lg) {
			margin-top: $large-margin;
		}

		@include media-breakpoint-up(xxl) {
			margin-top: ($default-margin * 2);
		}
	}

	&__rodeo-banner {
		height: 360px;
		position: relative;
		margin-bottom: ($default-margin * 2);
		display: flex;
		justify-content: center;
		align-items: center;

		@include media-breakpoint-up(lg) {
			height: 400px;
			margin-bottom: ($default-margin * 3);
		}

		@include media-breakpoint-up(xxl) {
			height: 480px;
			margin-bottom: ($default-margin * 5);
		}

		&__images {
			position: absolute;
			width: 100%;
			height: 100%;
		}

		&__image {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-size: cover;
			display: none;
			background-position: center center;

			&.current {
				display: block;
			}
		}

		&__cta {
			@include title;
			font-weight: 700;
			position: absolute;
			z-index: 2;
			color: $color-white;
			border-bottom: 2px solid $color-white;
			cursor: pointer;

			&:hover {
				opacity: 0.6;
			}
		}
	}

	&__notfound {
		text-align: center;

		p {
			margin-bottom: $default-margin;
		}
	}
}