.homepage {
	padding-top: 55px;

	@include media-breakpoint-up(xxl) {
		padding-top: 35px;
	}

	&__hero {
		@include make-container( $grid-container-widths );
		position: relative;
		display: flex;
		padding-top: ($default-padding * 2);
        padding-bottom: ($default-padding * 2);
		justify-content: center;
		align-items: center;

		@include media-breakpoint-up(lg) {
			padding-top: ($default-padding * 3);
			padding-bottom: ($default-padding * 3);
		}

		@include media-breakpoint-up(xxl) {
			padding-top: ($default-padding * 5);
			padding-bottom: ($default-padding * 5);
		}

		.post-detail {
			&__header {
				height: 400px;
				width: 100%;

				@include media-breakpoint-up(lg) {
					height: 540px;
				}

				@include media-breakpoint-up(xxl) {
					height: 600px;
				}

				.admin-bar & {
					height: 400px;

					@include media-breakpoint-up(lg) {
						height: 540px;
					}

					@include media-breakpoint-up(xxl) {
						height: 600px;
					}
				}

				&__title {
					@include very-large-title;
				}

				&__subtitle {
					@include serif-title-smaller;
				}

				&__link {
					background-image: linear-gradient( transparent calc( 100% - 2px ), $color-dark-grey 2px, $color-dark-grey );
					background-repeat: no-repeat;
					background-size: 0 100%;
					background-position: 0 -7px;

					@include media-breakpoint-up(xxl) {
						background-image: linear-gradient( transparent calc( 100% - 4px ), $color-dark-grey 4px, $color-dark-grey );
						background-position: 0 -9px;
					}
				}

				&.white {
					.post-detail__header__link {
						background-image: linear-gradient( transparent calc( 100% - 2px ), $color-white 2px, $color-white );
						background-position: 0 -7px;

						@include media-breakpoint-up(xxl) {
							background-image: linear-gradient( transparent calc( 100% - 4px ), $color-white 4px, $color-white );
						}
					}
				}

				&__content {
					max-width: 375px;

					@include media-breakpoint-up(md) {
						max-width: 540px;
					}

					@include media-breakpoint-up(lg) {
						max-width: 720px;
					}

					@include media-breakpoint-up(xxl) {
						max-width: 960px;
					}
				}
			}
		}
	}

	&__featured {
		@include make-container( $grid-container-widths );
        padding-bottom: ($default-padding * 2);

		@include media-breakpoint-up(lg) {
			padding-bottom: ($default-padding * 3);
		}

		@include media-breakpoint-up(xxl) {
			padding-bottom: ($default-padding * 5);
		}
	}
}
