.navigation {
	$root: &;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	visibility: hidden;
	overflow:0;
	width: 100%;
	height: 100%;
	padding: $default-padding $default-padding ($default-padding * 2);
	opacity: 0;
	background-color: $color-dark-grey;
	
	@include media-breakpoint-up(lg) {
		align-items: center;
	}

    &--animate {
    	animation-name: animateOut;
		animation-duration: 300ms;
		animation-fill-mode: forwards;
    }

	.navigation-opened & {
		animation-name: animateIn;
	}

	.admin-bar & {
		top: 32px;
		height: calc(100% - 32px);

		@media screen and (max-width:782px) {
			top: 46px;
			height: calc(100% - 46px);
		}
	}

	@include media-breakpoint-up(lg) {
	}

	@include media-breakpoint-up(xxl) {
	}

	.header-white &,
	.navigation-opened & {
		color: $color-white;
	}

	&__container {
		@include make-container( $grid-container-widths );
    	display: flex;
    	width: 100%;
    	flex-wrap: wrap;
    	max-width: 1600px;

    	@include media-breakpoint-up(lg) {
    		flex-wrap: nowrap;
    	}
	}

	&__newsletter {
		width: 100%;

		@include media-breakpoint-up(lg) {
			width: 40%;
		}

		.branding {
			position: absolute;
			top: 4px;
			left: 0;
			right: 0;

			@include media-breakpoint-up(lg) {
				display: block;
				margin-top: 5px;
				position: static;
			}

			&__logo {
				margin: 0 auto;

				@include media-breakpoint-up(xxl) {
					width: 310px;
					height: 81px;
					margin: 0;
				}

				svg {
					fill: $color-white;
				}
			}
		}
	}

	&__navigation {
		display: flex;
		width: 100%;
		max-width: 360px;
		margin-top: ($default-margin * 5);
		margin-right: auto;
		margin-bottom: -$default-margin;
		margin-left: auto;
		flex-wrap: wrap;

		@include media-breakpoint-up(lg) {
			width: 60%;
			max-width: 100%;
		}

		@include media-breakpoint-up(xxl) {
			margin-top: ($default-margin * 10);
		}
	}

	&__column {
		margin-bottom: $default-margin;

		@include media-breakpoint-up(lg) {
			margin-bottom: ($default-margin * 2);
		}

		@include media-breakpoint-up(xxl) {
			margin-bottom: ($default-margin * 3);
		}
	}

	&__primary {
		width: 100%;

		@include media-breakpoint-up(lg) {
			width: auto;
		}
	}

	&__menu {
		display: flex;
		flex-direction: column;
		width: 100%;
		text-align: center;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;

		@include media-breakpoint-up(lg) {
			text-align: left;
			justify-content: flex-start;
			align-items: flex-start;
		}
	}

	&__item + &__item {
		margin-top: $default-margin;
		margin-left: 0;

		@include media-breakpoint-up(xxl) {
			margin-top: $default-margin + 5px;
		}
	}

	&__item {
		&--active {
			#{$root}__link {
				border-bottom: 1px solid $color-dark-grey;

				.header-white & {
					border-bottom: 1px solid $color-white;
				}
			}
		}
		
		&--parent {
			&--active {
				#{$root}__link {
					border-bottom: 1px solid $color-dark-grey;

					.header-white & {
						border-bottom: 1px solid $color-white;
					}
				}
			}
		}

		&--hide-desktop {
			line-height: 18px;
			margin-top: $very-large-margin!important;

			#{$root}__link {
				font-size: 18px;
				line-height: 18px;
				display: inline-block;
				padding: 0;
				border-bottom: 2px solid $color-white;
			}

			@include media-breakpoint-up(lg) {
				display: none;
			}
		}

		&--hide-desktop + &--hide-desktop {
			margin-top: $default-margin!important;
		}

		&--small {
			margin-top: auto;
		}
	}

	&__link {
		font-size: 36px;
		font-weight: 700;
		line-height: 1em;
		padding: 3px 0;
		white-space: nowrap;

		@include media-breakpoint-up(sm) {
			font-size: 48px;
		}

		@include media-breakpoint-up(lg) {
			font-size: 24px;
		}

		@include media-breakpoint-up(xxl) {
			font-size: 28px;
		}

		&:hover {
			opacity: 0.6;
		}	
	}

	&__widgets {
		display: none;
		color: $color-grey;

		@include media-breakpoint-up(lg) {
			display: block;
			margin-left: $default-margin * 4;
		}

		@include media-breakpoint-up(xxl) {
			margin-left: $default-margin * 8;
		}
	}
}
