.branding {
	$root: &;
	position: relative;

	&__logo {
		width: 172px;
		height: 50px;
		display: block;

		@include media-breakpoint-up(sm) {
			width: 192px;
			height: 50px;
		}

		@include media-breakpoint-up(xxl) {
			width: 232px;
			height: 61px;
		}

		svg {
			width: 100%;
			height: auto;

			.header-white &,
			.navigation-opened & {
				fill: $color-white;
			}
		}
	}
}
