.footer {
	&__widgets {
		padding: ($default-padding * 2) 0;
		color: $color-grey;
		background-color: $color-dark-grey;

		@include media-breakpoint-up(lg) {
			padding: ($default-padding * 4) 0;
		}

		@include media-breakpoint-up(xxl) {
			padding: ($default-padding * 6) 0;
		}

		&__container {
			@include make-container( $grid-container-widths );
        	@include make-container-max-widths();
        	display: flex;
        	flex-wrap: wrap;
		}

		&__newsletter {
			width: 100%;

			@include media-breakpoint-up(md) {
				width: 40%;
			}

			.branding {
				@include media-breakpoint-up(lg) {
					margin-top: 5px;
				}

				&__logo {
					margin: 0 auto;

					svg {
						fill: $color-white;
					}
				}
			}
		}

		&__navigation {
			display: flex;
			width: 100%;
			margin-top: ($default-margin *2);
			margin-bottom: -$default-margin;
			flex-wrap: wrap;
			max-width: 360px;
			margin-left: auto;
			margin-right: auto;

			@include media-breakpoint-up(md) {
				width: 60%;
				margin-top: 0;
				margin-bottom: -($default-margin * 2);
				max-width: 100%;
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: -($default-margin * 3);
			}

			&__column {
				width: 50%;
				margin-bottom: $default-margin;

				@include media-breakpoint-up(lg) {
					margin-bottom: ($default-margin * 2);
				}

				@include media-breakpoint-up(xxl) {
					margin-bottom: ($default-margin * 3);
				}
			}
		}
	}

	&__copyright {
		@include make-container( $grid-container-widths );
		width: 100%;
		text-align: center;
		color: $color-grey;
		background-color: #151514;

		&__text {
			font-size: 12px;
			line-height: 16px;
			padding: ($default-padding * 2) 0;
			letter-spacing: 0.3px;

			@include media-breakpoint-up(lg) {
				padding: ($default-padding * 3) 0;
			}

			@include media-breakpoint-up(xxl) {
				font-size: 14px;
				line-height: 18px;
				padding: ($default-padding * 4) 0;
			}

			a {
				padding: 1px 0;
				color: $color-white;
				border-bottom: 1px solid $color-white;

				&:hover {
					color: $color-grey;
					border-color: $color-grey;
				}
			}
		}
	}
}
