.map {
	position: relative;

	&__canvas {
		width: 100%;
		height: 320px;

		@include media-breakpoint-up(lg) {
			height: 520px;
		}

		@include media-breakpoint-up(xxl) {
			height: 720px;
		}

		.mapboxgl-ctrl-group:not(:empty) {
			box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
		}
	}

	&__marker {
		i {
			display: block;
			width: 48px;
			height: 48px;
			background-color: $color-grey;
			border-radius: 24px;
			color: $color-white;
			text-align: center;
			padding: 14px 0;
			font-size: 32px;
			transform: scale(0.6);
			transform-origin: center center;
			transition-duration: 500ms;
			opacity: 0.8;
		}

		&.active i {
			background-color: $color-black;
			transform: scale(1);
			opacity: 1;
		}
	}

	&__legend {
		max-height: 500px;
		background-color: $color-white;
		padding: $default-padding + 2 $default-padding $default-padding + 5;
		border: 10px solid #f6f6f4;

		@include media-breakpoint-up(md) {
			width: 320px;
			position: absolute;
			top: $default-margin;
			right: $default-margin;
			border: 0;
		}

		@include media-breakpoint-up(xxl) {
			top: $very-large-margin;
			right: $very-large-margin;
		}

		&__list {
			counter-reset: legend;
		}

		&__item {
			&__title {
				@include serif-font;
				font-size: 20px;
				cursor: pointer;
				opacity: 0.5;
				line-height: 1em;

				.active & {
					opacity: 1;
				}

				&:before {
					counter-increment: legend;
					content: counter(legend);
					width: 30px;
					display: inline-block;
				}
			}

			&__info {
				font-size: 14px;
				line-height: 18px;
				max-height: 0;
				transition-property: max-height;
				transition-duration: 500ms;
				will-change: max-height;
				overflow: hidden;

				.active & {
					max-height: 300px;
				}

				margin-left: $large-margin + 2px;
			}

			&__address {
				display: block;
				margin-top: 5px;
			}

			&__description {
				margin-top: $mini-margin;
			}

			&__link {
				margin-top: $mini-margin;
			}
		}

		&__item + &__item {
			margin-top: $default-margin - 5;
		}
	}
}