.search-form {
	display: inline-flex;
	max-width: 200px;
	padding: 0 0 6px 0;
	border-bottom: 1px solid rgba($color-grey, 0.4);
	align-items: center;

	&__submit {
		padding: 0;
		cursor: pointer;
		background-color: transparent;
		margin: 0;
		appearance: none;
		height: 12px;

		svg {
			fill: $color-red;
			width: 12px;
			height: 12px;
		}
	}

	@include media-breakpoint-down(md) {
		margin-top: 20px;
	}
}