.subfooter {
	padding: ($default-padding * 2) 0;
	background-color: $color-white;

	@include media-breakpoint-up(md) {
		padding: ($default-padding * 4) 0;
	}

	@include media-breakpoint-up(xxl) {
		padding: ($default-padding * 6) 0;
	}

	&__container {
		@include make-container( $grid-container-widths );
    	@include make-container-max-widths();
    	display: flex;
    	flex-direction: column;
    	text-align: center;
    	flex-wrap: wrap;
    	align-items: center;
	}

	&__logo {
		width: 48px;
		height: 48px;

		@include media-breakpoint-up(lg) {
			width: 72px;
			height: 72px;
		}

		@include media-breakpoint-up(xxl) {
			width: 96px;
			height: 96px;
		}

		svg {
			width: 100%;
			height: auto;
			fill: $color-grey;
		}
	}

	&__text {
		@include serif-title-smaller;
		margin-top: $default-margin;
		color: $color-grey;

		@include media-breakpoint-up(lg) {
			margin-top: ($default-margin * 2);

			br {
				display: inline;
			}
		}

		@include media-breakpoint-up(xxl) {
			margin-top: ($default-margin * 3);
		}
	}

	&__cta {
		@include title;
		font-weight: 700;
		display: inline-block;
		margin-top: $default-margin;
		padding: 5px 0;
		border-bottom: 2px solid $color-dark-grey;

		&:hover {
			color: $color-grey;
			border-color: $color-grey;
		}

		@include media-breakpoint-up(lg) {
			margin-top: ($default-margin * 3);
		}

		@include media-breakpoint-up(xxl) {
			margin-top: ($default-margin * 4);
		}
	}
}