@font-face {
    font-family: "freshlabels";
    src: url('assets/fonts/freshlabels.eot');
    src: url('assets/fonts/freshlabels.eot?#iefix') format('eot'),
        url('assets/fonts/freshlabels.woff') format('woff2'),
        url('assets/fonts/freshlabels.woff') format('woff'),
        url('assets/fonts/freshlabels.ttf') format('truetype'),
}

@mixin icon-styles {
    font-family: "freshlabels";
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
    text-decoration: none;
    text-transform: none;
}

%icon {
    @include icon-styles;
}

@function icon-char($filename) {
    $char: "";

    @if $filename == icon-arrow-down {
        $char: "\E001";
    }
    @if $filename == icon-arrow-left {
        $char: "\E002";
    }
    @if $filename == icon-arrow-right {
        $char: "\E003";
    }
    @if $filename == icon-arrow-up {
        $char: "\E004";
    }

    @return $char;
}

@mixin icon($filename, $insert: before, $extend: true) {
    @include icon-styles;
    content: icon-char($filename);
}

.icon-icon-arrow-down {
    @include icon(icon-arrow-down);
}
.icon-icon-arrow-left {
    @include icon(icon-arrow-left);
}
.icon-icon-arrow-right {
    @include icon(icon-arrow-right);
}
.icon-icon-arrow-up {
    @include icon(icon-arrow-up);
}