.featured-navigation {
	$root: &;
	display: none;

	@include media-breakpoint-up(lg) {
		display: block;
		position: absolute;
		top: 32px;
		right: 80px;
		bottom: auto;
		left: auto;
		visibility: visible!important;
		width: auto;
		height: auto!important;
		margin-top: 0;
		padding: 0;
		padding: 0;
		animation-name: none;
		opacity: 1!important;
		background-color: transparent;
		align-items: flex-start;
		justify-content: flex-end;
	}

	@include media-breakpoint-up(xxl) {
		top: 30px;
		right: 90px;
	}

	.header-white &,
	.navigation-opened & {
		color: $color-white;
	}

	&__menu {
		display: flex;
		flex-direction: column;
		width: 100%;
		text-align: center;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;

		@include media-breakpoint-up(lg) {
			flex-direction: row;
			width: auto;
			text-align: left;
			align-items: flex-start;
			justify-content: flex-start;
		}
	}

	&__item + &__item {
		margin-top: $large-margin;
		margin-left: 0;

		@include media-breakpoint-up(sm) {
			margin-top: ($default-margin * 2);
		}

		@include media-breakpoint-up(lg) {
			margin-top: 0;
			margin-left: $default-margin;
		}

		@include media-breakpoint-up(xl) {
			margin-left: $large-margin;
		}

		@include media-breakpoint-up(xxl) {
			margin-left: ($default-margin * 2);
		}
	}

	&__item {
		&--active {
			#{$root}__link {
				border-bottom: 1px solid $color-dark-grey;

				.header-white & {
					border-bottom: 1px solid $color-white;
				}
			}
		}
		
		&--parent {
			&--active {
				#{$root}__link {
					border-bottom: 1px solid $color-dark-grey;

					.header-white & {
						border-bottom: 1px solid $color-white;
					}
				}
			}
		}

		&--hide-desktop {
			margin-top: $very-large-margin!important;
			line-height: 18px;

			#{$root}__link {
				font-size: 18px;
				line-height: 18px;
				border-bottom: 2px solid $color-white;
				padding: 0;
				display: inline-block;
			}

			@include media-breakpoint-up(lg) {
				display: none;
			}
		}

		&--hide-desktop + &--hide-desktop {
			margin-top: $default-margin!important;
		}

		&--small {
			margin-top: auto;
		}
	}

	&__link {
		font-size: 36px;
		font-weight: 700;
		line-height: 1em;
		padding: 3px 0;

		@include media-breakpoint-up(sm) {
			font-size: 48px;
		}

		@include media-breakpoint-up(lg) {
			font-size: 16px;
		}

		@include media-breakpoint-up(xxl) {
			font-size: 18px;
		}

		@include media-breakpoint-up(xxxl) {
			font-size: 20px;
		}	

		&:hover {
			opacity: 0.6;
		}	
	}
}
