@mixin placeholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content
    }

    &::-moz-placeholder {
        @content
    }

    &:-ms-input-placeholder {
        @content
    }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
textarea:-webkit-autofill:active,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus,
select:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0px 1000px white inset !important;
    -webkit-text-fill-color: $color-beige;
    color: $color-beige;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    -moz-appearance:textfield;
}

input[type="text"], input[type="email"], input[type="tel"], input[type="number"], textarea {
	border-radius: 0;
	outline: none;
	background-color: transparent;
  font-family: museo-sans, sans-serif;
	appearance: none;

    &::selection {
        background: $color-medium-grey;

        &:after {
            background-color: $color-white!important;
        }
    }

	@include placeholder {
        font-weight: 400;
        color: $color-grey;
        opacity: 1;
    }

    &.wpcf7-not-valid {
        border-bottom-color: $color-red;
    }
}

label {
    padding: 5px 0;
}

textarea {
	resize: none;
}

input[type="submit"] {
	cursor: pointer;
	border-radius: 0;
	appearance: none;
}

[type="checkbox"]:not(:checked), [type="checkbox"]:checked, [type="radio"]:not(:checked), [type="radio"]:checked {
    position: absolute;
    left: -9999px;
}

[type="checkbox"]:not(:checked)+label, [type="checkbox"]:checked+label, label [type="checkbox"]:not(:checked)+span, label [type="checkbox"]:checked+span, [type="radio"]:not(:checked)+label, [type="radio"]:checked+label {
    line-height: 20px;

    position: relative;
    display: inline-block;

    padding-left: 30px;

    cursor: pointer;
    user-select: none;
}

[type="checkbox"]:not(:checked)+label:before, [type="checkbox"]:checked+label:before, label [type="checkbox"]:not(:checked)+span:before, label [type="checkbox"]:checked+span:before, [type="radio"]:not(:checked)+label:before, [type="radio"]:checked+label:before {
    position: absolute;
    top: 0;
    left: 0px;
    display: inline-block;

    width: 20px;
    height: 20px;
    border: 1px solid $color-black;

    content: '';

    border-radius: 0;
    background-color: rgba(255, 255, 255, 0.2);
}

[type="checkbox"]:checked+label:before, label [type="checkbox"]:checked+span:before, [type="radio"]:checked+label:before {
	background-image: url(data:image/svg+xml;base64,PHN2ZyBlbmFibGUtYmFja2dyb3VuZD0ibmV3IC00OSAxNDEgNTEyIDUxMiIgdmlld0JveD0iLTQ5IDE0MSA1MTIgNTEyIiB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPjxwYXRoIGQ9Im0yMjMuOSA0OTYuOC03NCA3NC03NC4xLTc0LTg1LjUtODUuNiA3NC03NCA4NS42IDg1LjYgMTk5LjYtMTk5LjYgNzQgNzR6IiBmaWxsPSIjMmQ0NjRjIi8+PC9zdmc+);
	background-size: 13px 13px;
	background-color: $color-white;
	background-position: center center;
    background-repeat: no-repeat;
}

select {
    font-family: 'Pano', sans-serif;
    -webkit-appearance: none;
    padding: 5px;
    font-weight: 700;
    font-size: 14px;
}

.selectric {
    font-family: 'Pano', sans-serif;
    border: 0;
    border-bottom: 1px solid $color-black;
    background-color: transparent;

    .label {
        line-height: 26px;
        height: 26px;
        color: $color-beige;
        margin-left: 0;
        text-transform: none;
        font-size: 14px;
    }

    .arrow {
        font-size: 10px;
        height: 27px;
        width: 20px;

        &:after {
            content: '';
            border: 0;
            width: 8px;
            height: 6px;
            background-image: url(data:image/svg+xml;base64,);
            transition-property: transform;
            transition-duration: 300ms;

            .selectric-open & {
                transform: rotate(180deg);
            }
        }
    }
}

.selectric-items {
    font-family: 'Pano', sans-serif;
    margin-top: -1px;
    background-color: $color-white;

    ul {
        li {
            padding: 5px $mini-padding;

            &.highlighted {
                background-color: $color-light-grey;
            }
        }
    }
}

.room-type-select {
    width: 255px;
}
