.posts {
	margin-bottom: -($default-margin * 2);

	@include media-breakpoint-up(lg) {
		margin-bottom: -($default-margin * 3);
	}

	@include media-breakpoint-up(xxl) {
		margin-bottom: -($default-margin * 5);
	}

	&__post {
		width: 100%;
		padding-bottom: ($default-padding * 2);

		@include media-breakpoint-up(md) {
			width: 50%;
		}

		@include media-breakpoint-up(lg) {
			padding-bottom: ($default-padding * 3);
		}

		@include media-breakpoint-up(xxl) {
			padding-bottom: ($default-padding * 5);
		}

		&:nth-child(2n) {
			padding-left: ($default-padding * 2);

			@include media-breakpoint-up(lg) {
				padding-left: ($default-padding * 3);
			}

			@include media-breakpoint-up(xxl) {
				padding-left: ($default-padding * 5);
			}
		}

		&__image {
			&__link {
				display: block;

				&:hover {
					opacity: 0.85;
				}
			}

			img {
				width: 100%;
				height: auto;
				display: block;
				will-change: transform;
			}
		}

		&__header {
			margin-top: $default-margin - 5;

			@include media-breakpoint-up(sm) {
				margin-top: $default-margin;
			}

			@include media-breakpoint-up(lg) {
				margin-top: $large-margin;
			}
		}

		&__title {
			@include title;
			
			&__link {
				display: inline-block;

				.pointer-device &:hover {
					span,
					strong {
						&:after {
							transform: scale(1, 1);
						}
					}

					span + br + strong {
						&:after {
							transition-delay: 200ms;
						}
					}

					span {
						&:after {
							transition-delay: 0ms;
						}
					}
				}
			}

			span,
			strong {
				position: relative;
				display: inline-block;

				&:after {
					position: absolute;
					bottom: 1px;
					left: 0;
					width: 100%;
					height: 1px;
					background-color: $color-dark-grey;
					content: '';
					display: block;
					transform: scale(0, 1);
					transition-duration: 300ms;
					transform-origin: left top;

					.white & {
						background-color: $color-white;
					}
				}
			}

			span {
				font-family: 'Reckless Neue', sans-serif;
				font-weight: 400;

				&:after {
					transition-delay: 200ms;
					content: '\A';
					white-space: pre;
				}
			}
		}

		&__meta {
			margin-top: 5px;
		}
	}
}
