.post-carousel {
	display: flex;
	will-change: transform;

	&__post {
		display: flex!important;
		align-items: center;
		width: 600px;

		&__content {
			padding: 0 $default-padding;
			width: 100%;

			@include media-breakpoint-up(xxl) {
				padding: 0 $very-large-padding;
			}
		}

		&__image {
			&__link {
				display: block;

				&:hover {
					opacity: 0.85;
				}
			}
			
			img {
				width: 100%;
				height: auto;
				display: block;
				will-change: transform;
			}
		}

		&__header {
			margin-top: $large-margin;
			text-align: center;
		}

		&__title {
			@include mini-title;
			
			&__link {
				display: inline-block;

				&:hover {
					span,
					strong {
						&:after {
							transform: scale(1, 1);
						}
					}

					span + br + strong {
						&:after {
							transition-delay: 200ms;
						}
					}

					span {
						&:after {
							transition-delay: 0ms;
						}
					}
				}
			}

			span,
			strong {
				position: relative;
				display: inline-block;

				&:after {
					position: absolute;
					bottom: 0;
					left: 0;
					width: 100%;
					height: 1px;
					background-color: $color-dark-grey;
					content: '';
					display: block;
					transform: scale(0, 1);
					transition-duration: 300ms;
					transform-origin: left top;

					.white & {
						background-color: $color-white;
					}
				}
			}

			span {
				font-family: 'Reckless Neue', sans-serif;
				font-weight: 400;

				&:after {
					transition-delay: 200ms;
					content: '\A';
					white-space: pre;
				}
			}
		}
	}
}