.widget-menu {
	$root: &;

	&__item {
		&--social,
		&--highlight {
			color: $color-white;

			#{$root}__link {
				&:hover {
					color: $color-grey;
				}
			}
		}

		&--social {
			@include serif-title-smaller;
		}
	}

	&__item + &__item {
		margin-top: 5px;
	}

	&__link {
		font-weight: 700;

		&:hover {
			color: $color-white;
		}
	}
}