@include font-face('Reckless Neue', 'assets/fonts/RecklessNeue-Regular', 400, normal, otf eot woff woff2);
@include font-face('Circular', 'assets/fonts/lineto-circular-pro-book', 400, normal, eot woff woff2);
@include font-face('Circular', 'assets/fonts/lineto-circular-pro-bold', 700, normal, eot woff woff2);

@mixin serif-font {
    font-family: 'Reckless Neue', serif;
    font-feature-settings: 'ss04', 'ss05', 'ss06', 'ss07', 'ss08', 'ss12', 'ss13', 'liga' 0, 'dlig' 0;
}

@mixin large-serif-title {
    @include serif-font;
    font-size: 30px;
    font-weight: 400;
    line-height: 1.2em;
    letter-spacing: 0.01em;

    @include media-breakpoint-up(sm) {
        font-size: 36px;
    }

    @include media-breakpoint-up(md) {
        font-size: 48px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 64px;
    }

    @include media-breakpoint-up(xxl) {
        font-size: 80px;
    }
}

@mixin medium-serif-title {
    @include serif-font;
    font-size: 28px;
    font-weight: 400;
    line-height: 1.2em;
    letter-spacing: 0.01em;

    @include media-breakpoint-up(sm) {
        font-size: 30px;
    }

    @include media-breakpoint-up(md) {
        font-size: 36px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 48px;
    }

    @include media-breakpoint-up(xxl) {
        font-size: 64px;
    }
}

@mixin serif-title {
    @include serif-font;
    font-size: 24px;
    font-weight: 400;
    line-height: 1.25em;
    letter-spacing: 0.01em;

    @include media-breakpoint-up(sm) {
        font-size: 30px;
    }

    @include media-breakpoint-up(md) {
        font-size: 36px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 40px;
    }

    @include media-breakpoint-up(xxl) {
        font-size: 45px;
    }
}

@mixin serif-title-smaller {
    @include serif-font;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.4em;
    letter-spacing: 0.01em;

    @include media-breakpoint-up(sm) {
        font-size: 18px;
    }

    @include media-breakpoint-up(md) {
        font-size: 20px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 24px;
    }

    @include media-breakpoint-up(xxl) {
        font-size: 30px;
    }
}

@mixin mega-title {
    font-family: 'Circular', sans-serif;
    font-size: 36px;
    line-height: 1em;

    @include media-breakpoint-up(sm) {
        font-size: 56px;
    }

    @include media-breakpoint-up(md) {
        font-size: 90px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 110px;
    }

    @include media-breakpoint-up(xxl) {
        font-size: 120px;
    }
}

@mixin very-large-title {
    font-size: 24px;
    line-height: 1.1em;

    @include media-breakpoint-up(sm) {
        font-size: 28px;
    }

    @include media-breakpoint-up(md) {
        font-size: 36px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 56px;
    }

    @include media-breakpoint-up(xxl) {
        font-size: 90px;
    }
}

@mixin large-title {
    font-size: 18px;
    line-height: 1.25em;

    @include media-breakpoint-up(sm) {
        font-size: 20px;
    }

    @include media-breakpoint-up(md) {
        font-size: 24px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 28px;
    }

    @include media-breakpoint-up(xxl) {
        font-size: 36px;
    }
}

@mixin title {
    font-size: 18px;
    line-height: 1.25em;

    @include media-breakpoint-up(md) {
        font-size: 20px;
    }

    @include media-breakpoint-up(lg) {
        font-size: 24px;
    }

    @include media-breakpoint-up(xxl) {
        font-size: 28px;
    }
}

@mixin mini-title {
    font-size: 16px;
    line-height: 1.4em;

    @include media-breakpoint-up(md) {
        font-size: 18px;
    }

    @include media-breakpoint-up(xxl) {
        font-size: 20px;
    }
}

a {
    transition-duration: 500ms;
    transition-property: opacity, color, border;
}

strong {
    font-weight: 700;
}

p {
    + p {
        margin-top: 25px;

        @include media-breakpoint-up(lg) {
            margin-top: 28px;
        }

        @include media-breakpoint-up(xxl) {
            margin-top: 32px;
        }
    }
}

mark {
    color: $color-red;
    background-color: $color-light-red;
}

h1 {
    font-size: 48px;
    font-weight: 900;
    line-height: 57px;
}

h2, h3, h4 {
    font-weight: 700;
}

h2 {
    font-size: 38px;
    line-height: 46px;
}

h3 {
    font-size: 26px;
    line-height: 31px;
}

h4 {
    font-size: 20px;
    line-height: 24px;
    color: $color-medium-grey;
}
