.post-detail {
	width: 100%;

	&__progress {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 5px;
		z-index: 9;
		background-color: rgba($color-dark-grey, 0.5);

		.white & {
			background-color: rgba($color-white, 0.2);
		}

		&__bar {
			width: 100%;
			height: 5px;
			transform: scale(0, 1);
			transform-origin: left center;
			background-color: $color-dark-grey;

			.white & {
				background-color: $color-white;
			}
		}

		.admin-bar & {
			top: 32px;

			@media screen and (max-width:782px) {
				top: 46px;
			}
		}
	}

	&__header {
		position: relative;
		display: flex;
		height: 100vh;
		align-items: center;
		justify-content: center;
		background-color: $color-white;

		@media not all and (hover:hover) {
        	height: calc(100vh - 70px);
    	}

		.admin-bar & {
			height: calc( 100vh - 32px );

			@media screen and (max-width:782px) {
				height: calc( 100vh - 46px );
			}
		}

		&.white {
			background-color: $color-dark-grey;
		}

		&__image {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-position: center center;
			background-size: cover;
			opacity: 0;
			transition-duration: 500ms;
			transition-property: opacity;

			&:before {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				content: '';
				background-color: rgba($color-black, 0.2);
			}

			.image-loaded & {
				opacity: 1;
			}
		}

		&__content {
			position: relative;
			z-index: 2;
			max-width: 1080px;
			text-align: center;
			padding: 0 $mini-padding;

			.white & {
				color: $color-white;
			}
		}

		&__subtitle {
			@include serif-title;
			margin-bottom: 5px;
		}

		&__title {
			@include mega-title;
			position: relative;
		}

		&__link {
			background-image: linear-gradient( transparent calc( 100% - 3px ), $color-dark-grey 3px, $color-dark-grey );
			background-repeat: no-repeat;
			background-size: 0 100%;
			transition: background-size 500ms;
			background-position: 0 -8px;

			@include media-breakpoint-up(xxl) {
				background-image: linear-gradient( transparent calc( 100% - 5px ), $color-dark-grey 5px, $color-dark-grey );
				background-position: 0 -10px;
			}

			.white & {
				background-image: linear-gradient( transparent calc( 100% - 3px ), $color-white 3px, $color-white );
				background-position: 0 -8px;

				@include media-breakpoint-up(xxl) {
					background-image: linear-gradient( transparent calc( 100% - 5px ), $color-white 5px, $color-white );
				}
			}

			.pointer-device &:hover {
				background-size: 100% 100%;
			}

			.home-to-single & {
				background-size: 0 100%!important;
			}
		}

		&__meta {
			font-weight: 700;
			margin-top: $default-margin;

			.template-homepage & {
				transition-property: opacity;
				transition-duration: 500ms;
				transition-delay: 500ms;
			}

			.home-to-single.template-homepage & {
				opacity: 1;
			}
		}

		&__author {
			border-bottom: 1px solid $color-dark-grey;

			&:hover {
				opacity: 0.6;
			}

			.white & {
				border-bottom-color: $color-white;
			}
		}

		&__arrow {
			position: absolute;
			bottom: $default-margin;

			@include media-breakpoint-up(xxl) {
				bottom: $large-margin;
			}

			a {
				font-size: 48px;
				cursor: pointer;

				@include media-breakpoint-up(xxl) {
					font-size: 64px;
				}

				&:before {
					@include icon(icon-arrow-down);

					.white & {
						color: $color-white;
					}
				}
			}
		}
	}

	&__main {
		position: relative;
		padding: ($default-padding * 2) 0;

		@include media-breakpoint-up(lg) {
			padding: ($default-padding * 4) 0;
		}

		@include media-breakpoint-up(xxl) {
			padding: ($default-padding * 5) 0;
		}

		&--white {
			color: $color-white;
		}
	}

	&__breadcrumb {
		@include make-container( $grid-container-widths );
        @include make-container-max-widths();
	}

	&__next-post {
		position: fixed;
		top: -$very-large-margin;
		right: $default-margin;
		z-index: 9;
		display: none;
		border-radius: 50px;
		overflow: hidden;
		background-color: $color-black;
		opacity: 0;
		transition-property: opacity, visibility;
		transition-duration: 500ms;
		pointer-events: none;
		visibility: none;

		.white & {
			background-color: $color-white;
		}

		@include media-breakpoint-up(xxl) {
			top: 50%;
			right: ($default-margin * 4);
			display: block;
		}

		&.static {
			opacity: 1;
			visibility: visible;
			pointer-events: all;
		}

		&__image {
			position: absolute;
			top: 0;
			left: 0;
			width: 100px;
			height: 100px;
			opacity: 0;
			transition-duration: 500ms;
			transition-property: opacity;
		}

		&__link {
			display: block;
			text-align: center;
			color: $color-white;
			padding: $default-padding 0;
			line-height: 1em;
			position: relative;
			z-index: 2;

			@include media-breakpoint-up(xxl) {
				width: 100px;
				height: 100px;
				border-radius: 50px;
				padding: $large-padding - 2px 0;
			}

			&:hover {
				+ img {
					opacity: 0.3;
				}
			}
		}
	}

	&__intro {
		@include make-container( $grid-container-widths );
        @include make-container-max-widths();
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        @include media-breakpoint-up(lg) {
        	flex-wrap: nowrap;
        }

		&__text {
			@include serif-title;
		}

		&__share {
			flex-shrink: 0;
			text-align: center;
			margin-top: $large-margin;

			@include media-breakpoint-up(lg) {
				width: 200px;
			}

			@include media-breakpoint-up(xxl) {
				width: 280px;
			}

			&__list {
				display: flex;

				@include media-breakpoint-up(lg) {
					flex-direction: column;
				}
			}

			&__item + &__item {
				margin-left: $default-margin;

				@include media-breakpoint-up(lg) {
					margin-left: 0;
				}
			}

			&__link {
				font-weight: 700;
				font-size: 16px;
				text-decoration: underline;
				cursor: pointer;
				opacity: 0.5;

				&:hover {
					text-decoration: none;
					opacity: 1;
				}

				@include media-breakpoint-up(xxl) {
					font-size: 18px;
				}
			}
		}
	}	

	&__share {
		@include make-container( $grid-container-widths );
		margin-top: $default-margin * 2;

		@include media-breakpoint-up(lg) {
			margin-top: ($default-margin * 3);
		}

		@include media-breakpoint-up(xxl) {
			margin-top: ($default-margin * 4);
		}

		&__header {
			text-align: center;
		}

		&__title {
			@include medium-serif-title;
		}

		&__list {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			margin-top: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-top: ($default-margin * 2);
			}
		}

		&__item {

		}

		&__item + &__item {
			margin-left: $large-margin;

			@include media-breakpoint-up(lg) {
				margin-left: ($default-margin * 3);
			}
		}

		&__link {
			@include large-title;
			cursor: pointer;
			border-bottom: 2px solid $color-dark-grey;

			&:hover {
				opacity: 0.5;
			}

			.white & {
				border-bottom-color: $color-white;
			}
		}
	}

	//Section
	&__section {
		&:first-child,
		+ * {
			margin-top: $default-margin * 2;

			@include media-breakpoint-up(lg) {
				margin-top: ($default-margin * 3);
			}

			@include media-breakpoint-up(xxl) {
				margin-top: ($default-margin * 4);
			}
		}

		&--text {

		}

		&--blockquote {

		}

		&--fullwidth_image {
			+ *,
			* + & {
				@include media-breakpoint-up(xxl) {
					margin-top: ($default-margin * 5);
				}
			}
		}

		&--image_list {

		}

		&--gallery {
			+ *,
			* + & {
				@include media-breakpoint-up(xxl) {
					margin-top: ($default-margin * 5);
				}
			}
		}

		&--youtube-video {

		}

		&--freshlabels_products {
			+ *,
			* + & {
				@include media-breakpoint-up(xxl) {
					margin-top: ($default-margin * 5);
				}
			}
		}
	}

	//Blocks
	&__text {
		@include make-container( $grid-container-widths );
		max-width: 1200px;

		h2 {
			@include serif-title;
			margin-bottom: $mini-margin;
		}

		h3 {
			@include serif-title-smaller;
			margin-bottom: $mini-margin;
		}

		p {
			+ h2,
			+ h3 {
				margin-top: $default-margin;
			}

			+ ul {
				margin-top: $default-margin;

				@include media-breakpoint-up(lg) {
					margin-top: $large-margin;
				}
			}
		}

		ul {
			.white & {
				color: $color-white;
			}

			li {
				border-left: 3px solid $color-dark-grey;
				padding-left: $mini-margin;

				@include media-breakpoint-up(xxl) {
					border-left: 4px solid $color-dark-grey;
					padding-left: $mini-margin + 4;
				}

				.white & {
					border-color: $color-white!important;
				}

				+ li {
					margin-top: $mini-margin;

					@include media-breakpoint-up(lg) {
						margin-top: $default-margin;
					}
				}
			}

			+ p {
				margin-top: $default-margin;

				@include media-breakpoint-up(lg) {
					margin-top: $large-margin;
				}
			}
		}
	}

	&__title {
		@include make-container( $grid-container-widths );
		max-width: 1200px;
		text-align: center;

		h2 {
			@include serif-title;
		}
	}

	&__buttons {
		@include make-container( $grid-container-widths );
		max-width: 1200px;
		text-align: center;

		&__list {
			display: flex;
			justify-content: center;
		}

		&__item + &__item {
			margin-left: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-left: ($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-left: ($default-margin * 3);
			}
		}

		&__link {
			@include title;
			font-weight: 700;
			padding: 5px 0;
			color: $color-dark-grey;
			border-bottom: 2px solid $color-dark-grey;

			.white & {
				color: $white;
				border-color: $color-white;
			}

			&:hover {
				opacity: 0.5;
			}
		}
	}

	&__blockquote {
		@include make-container( $grid-container-widths );
		max-width: 1200px;

		&__content {
			@include serif-title;
		}
	}

	&__fullwidth-image {
		height: 420px;
		background-position: center center;
		background-size: cover;

		@include media-breakpoint-up(sm) {
			height: 500px;
		}

		@include media-breakpoint-up(lg) {
			height: 600px;
		}

		@include media-breakpoint-up(xxl) {
			height: 700px;
		}

		@include media-breakpoint-up(xxxl) {
			height: 900px;
		}
	}

	&__image {
		@include make-container( $grid-container-widths );
		max-width: 1200px;

		&__header {
			text-align: center;
			margin-bottom: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-bottom: ($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: ($default-margin * 3);
			}
		}

		&__title {
			@include medium-serif-title;
		}

		&__link {
			display: block;

			&:hover {
				opacity: 0.85;
			}
		}

		img {
			width: 100%;
			height: auto;
			display: block;
		}
	}

	&__image-list {
		@include make-container( $grid-container-widths );
		max-width: 1400px;

		&__header {
			text-align: center;
			margin-bottom: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-bottom: ($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: ($default-margin * 3);
			}
		}

		&__title {
			@include medium-serif-title;
		}

		&__item {
			display: flex;

			&__image {
				width: 80px;
				flex-shrink: 0;

				@include media-breakpoint-up(sm) {
					width: 120px;
				}

				@include media-breakpoint-up(lg) {
					width: 160px;
				}

				@include media-breakpoint-up(xxl) {
					width: 220px;
				}

				img {
					width: 100%;
					height: auto;
					display: block;
				}
			}

			&__content {
				margin-left: $default-margin;

				@include media-breakpoint-up(lg) {
					margin-left: ($default-margin * 2);
				}

				@include media-breakpoint-up(xxl) {
					margin-top: $default-margin;
					margin-left: ($default-margin * 3);
				}
			}

			&__title {
				@include large-title;
			}

			&__description {
				margin-top: $mini-margin;
			}
		}

		&__item + &__item {
			margin-top: $large-margin;

			@include media-breakpoint-up(lg) {
				margin-top: $very-large-margin;
			}
		}
	}

	&__gallery {
		@include make-container( $grid-container-widths );
		max-width: 1400px;

		&__header {
			text-align: center;
			margin-bottom: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-bottom: ($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: ($default-margin * 3);
			}
		}

		&__title {
			@include medium-serif-title;
		}

		&__images {
			margin-bottom: -$default-margin;
			opacity: 0;
			transition-duration: 500ms;
			transition-property: opacity;

			&.masonry--loaded {
				opacity: 1;
			}

			@include media-breakpoint-up(lg) {
				margin-bottom: -($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: -($default-margin * 3);
			}
		}

		&__image {
			padding-bottom: $default-padding;

			@include media-breakpoint-up(lg) {
				padding-bottom: ($default-padding * 2);
			}

			@include media-breakpoint-up(xxl) {
				padding-bottom: ($default-padding * 3);
			}

			&:nth-child(2n) {
				@include media-breakpoint-up(lg) {
					padding-left: ($default-padding * 3);
				}

				@include media-breakpoint-up(xxl) {
					padding-left: ($default-padding * 4);
				}
			}

			img {
				width: 100%;
				height: auto;
				display: block;
			}

			&__caption {
				text-align: center;
				font-size: 12px;
				opacity: 0.6;
				margin-top: $mini-margin;

				@include media-breakpoint-up(lg) {
					font-size: 14px;
				}
			}
		}
	}

	&__youtube-video {
		@include make-container( $grid-container-widths );
		max-width: 1400px;

		&__header {
			text-align: center;
			margin-bottom: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-bottom: ($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: ($default-margin * 3);
			}
		}

		&__title {
			@include medium-serif-title;
		}

		&__description {
			text-align: center;
			font-size: 12px;
			opacity: 0.6;
			margin-top: $mini-margin;

			@include media-breakpoint-up(lg) {
				font-size: 14px;
			}
		}
	}

	&__spotify {
		@include make-container( $grid-container-widths );
		max-width: 1200px;

		&__header {
			text-align: center;
			margin-bottom: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-bottom: ($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: ($default-margin * 3);
			}
		}

		&__title {
			@include medium-serif-title;
		}

		&__player {
			iframe {
				width: 100%;
			}

			&--show {
				height: 152px;

				iframe {
					height: 152px;
				}
			}

			&--track {
				height: 80px;

				iframe {
					height: 80px;
				}
			}

			&--playlist {
				height: 300px;

				iframe {
					height: 300px;
				}
			}
		}
	}

	&__soundcloud {
		@include make-container( $grid-container-widths );
		max-width: 1200px;

		&__header {
			text-align: center;
			margin-bottom: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-bottom: ($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: ($default-margin * 3);
			}
		}

		&__title {
			@include medium-serif-title;
		}

		&__player {
			iframe {
				width: 100%;
			}
		}
	}

	&__instagram {
		@include make-container( $grid-container-widths );
		max-width: 900px;

		&__header {
			text-align: center;
			margin-bottom: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-bottom: ($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: ($default-margin * 3);
			}
		}

		&__title {
			@include medium-serif-title;
		}

		iframe {
			width: 100%;
			max-width: 100%!important;

			.Footer {
				display: none;
			}
		}
	}

	&__freshlabels-products {
		@include make-container( $grid-container-widths );
		max-width: 1560px;

		&__header {
			text-align: center;
			margin-bottom: $default-margin;

			@include media-breakpoint-up(lg) {
				margin-bottom: ($default-margin * 2);
			}

			@include media-breakpoint-up(xxl) {
				margin-bottom: ($default-margin * 3);
			}
		}

		&__title {
			@include medium-serif-title;
		}

		&__row {
			display: flex;
			flex-wrap: wrap;
			margin-left: -$mini-margin;
			margin-right: -$mini-margin;
			margin-bottom: -$large-margin;

			@include media-breakpoint-up(lg) {
				margin-bottom: -($default-margin * 2);
			}
		}

		&__product {
			width: 50%;
			padding: 0 $mini-padding;
			margin-bottom: $large-margin;

			@include media-breakpoint-up(lg) {
				width: 25%;
				margin-bottom: ($default-margin * 2);
			}

			&__image {
				$fl-image: &;

				&__link {
					display: block;
					position: relative;

					&:hover {
						img {
							opacity: 0.6;
						}

						#{$fl_image}__overlay {
							opacity: 1;
						}
					}
				}

				&__overlay {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					opacity: 0;
					transition-property: opacity;
					transition-duration: 500ms;
					text-align: center;
					padding: $default-padding;
					z-index: 2;

					span {
						@include title;
						font-weight: 700;
						border-bottom: 2px solid $color-dark-grey;
					}

					p {
						@include title;
						font-weight: 700;

						i {
							background-image: linear-gradient( transparent calc( 100% - 2px ), $color-dark-grey 2px, $color-dark-grey );
						}
					}

					&--visible {
						opacity: 1;

						+ img {
							opacity: 0.6;
						}
					}
				}

				img {
					display: block;
					width: 100%;
					height: auto;
					transition-property: opacity;
					transition-duration: 500ms;
				}
			}

			&__header {
				margin-top: $default-margin;
				text-align: center;
			}

			&__title {
				@include mini-title;

				&__link {
					&:hover {
						text-decoration: underline;
					}
				}
			}

			&__price {
				font-family: 'Reckless Neue', sans-serif;
				opacity: 0.6;
			}
		}
	}
}