.breadcrumb {
    font-size: 14px;
    margin-bottom: $default-margin;
    display: flex;
    align-items: center;
    line-height: 24px;

    @include media-breakpoint-up(lg) {
        margin-bottom: $large-margin;
    }

    @include media-breakpoint-up(xxl) {
        margin-bottom: ( $default-margin * 2 );
    }

    &__item {
    	padding: 0;
    }

    > * + * {
        margin-left: $mini-margin;
        position: relative;
        display: flex;
        align-items: center;
        white-space: nowrap;

        @include media-breakpoint-up(xxl) {
            margin-left: $mini-margin;
        }

        &:before {
            content: '\003E';
            margin-right: $mini-margin;
            vertical-align: top;
            height: 16px;
            line-height: 16px;

            @include media-breakpoint-up(xxl) {
                margin-right: $mini-margin;
            }
        }
    }

    a {
    	span {
            display: block;
            height: 25px;
            border-bottom: 1px solid $color-dark-grey;

            .white & {
                border-bottom: 1px solid $color-white;
            }
        }

        &:hover {
            opacity: 0.6;
        }
    }

    span {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        flex-shrink: 1;

        span {
            border-bottom: 1px solid transparent;
            height: 25px;
        }
    }
}
