.active .map__legend__item__title { opacity: 1; }

.map__legend__item__title:before { counter-increment: legend; content: counter(legend); width: 30px; display: inline-block; }

.map__legend__item__info { font-size: 14px; line-height: 18px; max-height: 0; transition-property: max-height; transition-duration: 500ms; will-change: max-height; overflow: hidden; margin-left: 32px; }

.active .map__legend__item__info { max-height: 300px; }

.map__legend__item__address { display: block; margin-top: 5px; }

.map__legend__item__description { margin-top: 10px; }

.map__legend__item__link { margin-top: 10px; }

.map__legend__item + .map__legend__item { margin-top: 15px; }
