.full-screen-video {
	position: fixed;
	z-index: 1;
	top: 0; 
	left: 0;
	visibility: none;
	width: 100%;
	height: 100%;
	pointer-events: none;
	opacity: 0;
	transition-property: opacity, visibility, z-index;
	transition-duration: 1000ms;
	background-color: $color-black;

	.admin-bar & {
		top: 32px;
		height: calc( 100% - 32px );

		@media screen and (max-width:782px) {
			top: 46px;
			height: calc( 100% - 46px );
		}
	}

	&.visible {
		visibility: visible;
		pointer-events: all;
		opacity: 1;
		z-index: 999;
	}

	&__close {
		position: absolute;
		top: $default-margin;
		right: $default-margin;
		z-index: 10;

		&:before {
			background-image: url(data:image/svg+xml;base64,);
			width: 20px;
			height: 20px;
			content: '';
			background-size: 20px 20px;
			display: block;
		}
	}

	&__wrapper {
		position: relative;
		top: 50%;
		transform: translate3d(0, -50%, 0);
		padding-bottom: 50%; /* 16:9 */
		padding-top: 25px;
		height: 0;

		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
}